:host {
  padding: 0 0.75rem;
  display: block;
  // todo: move to a shared scss file
  $color-black-haze:#F7F8F8;
  $color-blue-eastern: #1C99BC;
  $color-gray-dusty : #979797;
  .clickable-text {
    cursor: pointer;
    // color: $color-blue-eastern;

    // color: var(--calcite-color-text-1);
    // text-decoration: underline solid var(--calcite-color-text-1); // $color-blue-eastern;
    font-weight: normal;
  
    &:hover {
      // color: $color-blue-eastern;
      // text-decoration: underline solid $color-blue-eastern;
    }
  }

  calcite-notice {
    margin-top: 6px;
  }

  .credits-info {
    // background: $color-black-haze;
    margin: 10px 0 10px 0;
    padding: 10px 0;
    background-color: var(--calcite-color-foreground-3);
    color: var(--calcite-color-text-2);
    display: flex;
    justify-content: space-between;
    // height: 39px;
    >div{
      margin: 0px 10px;
    }
    .show-credits {
        display: inline-flex;
        align-items: center;
      //   position: relative;
      //   top: 2px;
      survey123-ui-spinner {
          line-height: 0;
      }
      ::ng-deep span {
          line-height: 0;
      }
      .icon-spin.icon-refresh {
          top: 1px;
      }
      .spinner{
          -webkit-transition: none;
          -moz-transition: none;
          -o-transition: none;
          transition: none;
      }
      span:not(.clickable-text) {
          cursor: default;
      }
      span.clickable-text.disabled {
          // color: $color-gray-dusty;
          // color: var(--calcite-color-text-1);
          // text-decoration: underline solid var(--calcite-color-text-1); // $color-gray-dusty;
          cursor: not-allowed;
      }
    }
    .credits-result-info{
      flex-grow: 1;
      display: flex;
      justify-content: flex-end;
      span {
        word-break: break-word;
      }
    }
  }
  .pieview-info {
    margin-top: 10px;
    margin-bottom: 20px;
    .has-spinner >span {
        cursor: default;
        &.report-status {
          padding: 0 2px;
        }
    }
    .preview-text {
        cursor: default;
        display: flex;
        align-items: center;
        >span {
            cursor: pointer;
        }
    }
    .preview-text.disabled {
        text-decoration: underline solid $color-gray-dusty;
        color: $color-gray-dusty;
        cursor: not-allowed;
        >span {
            cursor: not-allowed;
        }
    }
  }
  .execute-btn {
    overflow: auto;
    margin: -5px 0 30px 0;
    >span {
        line-height: 41px;
    }
    a {
      float: inline-start;
        button {
            width: auto;
        }
    }
    &.portal-btn {
      margin: 20px 0;
    }
  }
  calcite-loader {
    display: inline-block;
    margin: 0 0 0 10px;
  }
}